*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    overflow-x: hidden;
}
nav{
    width: 100%;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}
.logo{
    padding: 1.5rem 0 1.5rem 2rem;
}
.hero-container{
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.hero-container h3{
    font-size: 1rem;
    font-weight: 300;
}
.hero-container img{
    padding-top: 2rem;
    max-width: 20rem;
    margin: 0 auto;
}
.hero-container h1{
    padding-top: 3rem;
    font-size: 1.5rem;
}
.text{
    padding: 0 0 2rem 1rem;
}
.card-container{
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    max-width: 570px;
    margin: auto;
    padding-bottom: 2rem;
}
.card{
    position: relative;
    width: 176px;
    margin: auto;
    font-size: 0.75rem;
}
.Status{
    position: absolute;
    top: 7px;
    left: 10px;
    padding: 0 3px;
    border-radius: 3px;
    font-weight: bold;
    background-color: white;
}
.card-image{
    width: 176px;
}
.star-color{
   color:  #FE395C;
}
.members{
    color: gray;
}
@media only screen and (min-width:768px){
    .text{
        padding-left: 3rem;
    }
    .text h3{
        max-width: 35rem;
    }
    .hero-container img{
        max-width: 30rem;
    }
    .hero-container h1{
        font-size: 2.25rem;
    }
    .card-container{
        grid-template-columns: repeat(3,1fr);
        gap: 1rem;
    }
}
@media only screen and (min-width:1280px){
    .text{
        padding-left: 6rem;
    }
    .text h1{
        font-size: 4rem;
    }
    .text h3{
        font-size: 1.5rem;
    }
    .text h3{
        max-width: 55rem;
    }
}
